<template>
	<div>
		<b-carousel
			id="carousel-1"
			v-model="slide"
			:interval="6000"
			:controls="true"
			background="#ababab"
			style="text-shadow: 1px 1px 2px #333; width:100%; height:auto"
			@sliding-start="onSlideStart"
			@sliding-end="onSlideEnd"
		>
			<!-- Text slides with image -->
			<b-carousel-slide
				:img-src="url_env + '/backassets/carrusel/nadieSeVaya.jpeg'"
			></b-carousel-slide>

			<b-carousel-slide
				:img-src="url_env + '/backassets/carrusel/tus_manos.png'"
			></b-carousel-slide>
		</b-carousel>
	</div>
</template>

<script>
export default {
	name: 'CarruselComponent',
	data() {
		return {
			slide: 0,
			sliding: null,
			url_env: '',
		};
	},
	mounted() {
		this.url_env = process.env.VUE_APP_URL;
	},
	methods: {
		onSlideStart() {
			this.sliding = true;
		},
		onSlideEnd() {
			this.sliding = false;
		},
	},
};
</script>
